import constants from './constants';

const logisticNameComparator = (nextComparator, logisticA, logisticB) => {
  let result = logisticA.chapter - logisticB.chapter;
  if (result === 0) {
    result = logisticA.mission - logisticB.mission;
    if (result === 0 && nextComparator) {
      return nextComparator(logisticA, logisticB);
    } else {
      return result;
    }
  } else {
    return result;
  }
};

const resourcesBasedComparatorFactory = (weights) => {
  return (nextComparator, logisticA, logisticB) => {
    const manpowerWeight = weights[constants.RESOURCES.MANPOWER];
    const ammoWeight = weights[constants.RESOURCES.AMMO];
    const rationsWeight = weights[constants.RESOURCES.RATIONS];
    const partsWeight = weights[constants.RESOURCES.PARTS];
    const totalResourcesWeight = weights.totalResources;

    let logisticAValue = 0;
    logisticAValue += manpowerWeight * logisticA.totalResources[constants.RESOURCES.MANPOWER];
    logisticAValue += ammoWeight * logisticA.totalResources[constants.RESOURCES.AMMO];
    logisticAValue += rationsWeight * logisticA.totalResources[constants.RESOURCES.RATIONS];
    logisticAValue += partsWeight * logisticA.totalResources[constants.RESOURCES.PARTS];
    logisticAValue += totalResourcesWeight * logisticA.totalResources.sum;

    let logisticBValue = 0;
    logisticBValue += manpowerWeight * logisticB.totalResources[constants.RESOURCES.MANPOWER];
    logisticBValue += ammoWeight * logisticB.totalResources[constants.RESOURCES.AMMO];
    logisticBValue += rationsWeight * logisticB.totalResources[constants.RESOURCES.RATIONS];
    logisticBValue += partsWeight * logisticB.totalResources[constants.RESOURCES.PARTS];
    logisticBValue += totalResourcesWeight * logisticB.totalResources.sum;

    const result = logisticBValue - logisticAValue;
    if (result === 0 && nextComparator) {
      return nextComparator(logisticA, logisticB);
    } else {
      return result;
    }
  };
};

const itemsBasedComparatorFactory = (weights) => {
  return (nextComparator, logisticA, logisticB) => {
    const tDollContractWeight = weights[constants.ITEMS.T_DOLL_CONTRACT];
    const equipContractWeight = weights[constants.ITEMS.EQUIP_CONTRACT];
    const quickProductionWeight = weights[constants.ITEMS.QUICK_PRODUCTION];
    const quickRepairWeight = weights[constants.ITEMS.QUICK_REPAIR];
    const tokenWeight = weights[constants.ITEMS.TOKEN];

    const getItemProbability = (logistic, item) => {
      if (logistic.items.indexOf(item) > -1) {
        return 2 / logistic.items.length;
      } else {
        return 0;
      }
    };

    let logisticAValue = 0;
    logisticAValue
      += tDollContractWeight
      * getItemProbability(logisticA, constants.ITEMS.T_DOLL_CONTRACT)
      * logisticA.occurrences;
    logisticAValue
      += equipContractWeight
      * getItemProbability(logisticA, constants.ITEMS.EQUIP_CONTRACT)
      * logisticA.occurrences;
    logisticAValue
      += quickProductionWeight
      * getItemProbability(logisticA, constants.ITEMS.QUICK_PRODUCTION)
      * logisticA.occurrences;
    logisticAValue
      += quickRepairWeight
      * getItemProbability(logisticA, constants.ITEMS.QUICK_REPAIR)
      * logisticA.occurrences;
    logisticAValue
      += tokenWeight * getItemProbability(logisticA, constants.ITEMS.TOKEN) * logisticA.occurrences;

    let logisticBValue = 0;
    logisticBValue
      += tDollContractWeight
      * getItemProbability(logisticB, constants.ITEMS.T_DOLL_CONTRACT)
      * logisticB.occurrences;
    logisticBValue
      += equipContractWeight
      * getItemProbability(logisticB, constants.ITEMS.EQUIP_CONTRACT)
      * logisticB.occurrences;
    logisticBValue
      += quickProductionWeight
      * getItemProbability(logisticB, constants.ITEMS.QUICK_PRODUCTION)
      * logisticB.occurrences;
    logisticBValue
      += quickRepairWeight
      * getItemProbability(logisticB, constants.ITEMS.QUICK_REPAIR)
      * logisticB.occurrences;
    logisticBValue
      += tokenWeight * getItemProbability(logisticB, constants.ITEMS.TOKEN) * logisticB.occurrences;

    const result = logisticBValue - logisticAValue;
    if (result === 0 && nextComparator) {
      return nextComparator(logisticA, logisticB);
    } else {
      return result;
    }
  };
};

const combineComparators = (comparatorsArray) => {
  const reducer = (acc, curr) => {
    return curr.bind(undefined, acc);
  };
  // using false because you cannot bind an argument to undefined
  // yes this overloads the argument type but this is the power of js truthy checks
  return comparatorsArray.reverse().reduce(reducer, false);
};

export default {
  combineComparators,
  logisticNameComparator,
  resourcesBasedComparatorFactory,
  itemsBasedComparatorFactory
};
