import constants from './constants';

// calculates resource per minute based on total resource
// across occurrences divided by total timespan
const _calculateEfficiency = (occurrences, timespan, resourcesMap) => {
  const retVal = {};
  const _efficiencyHelper = ({ hours, minutes }, resourceCount) => {
    return Number.parseFloat(((occurrences * resourceCount) / (hours * 60 + minutes)).toFixed(2));
  };
  Object.values(constants.RESOURCES).forEach((resource) => {
    retVal[resource] = occurrences ? _efficiencyHelper(timespan, resourcesMap[resource]) : 0;
  });
  return retVal;
};

// calculates number of occurrences that can occur in a given
// timespan given the interval at which logistics can start
const _calculateOccurrencesPerIntervalPerTimespan = (
  logisticDuration,
  activeInterval,
  timespan
) => {
  const durationInMinutes = logisticDuration.hours * 60 + logisticDuration.minutes;
  // if given a zero interval, the calculations will infinite loop
  // set it to the active logistic's duration so calculator behaves
  // as if user will immediately start another mission when one ends
  const intervalInMinutes = activeInterval.hours * 60 + activeInterval.minutes || durationInMinutes;
  const timespanInMinutes = timespan.hours * 60 + timespan.minutes;

  let retVal = 0;
  let timeCounter = 0;
  let intervalCounter = 0;
  while (timeCounter < timespanInMinutes) {
    while (intervalCounter < timeCounter) {
      intervalCounter += intervalInMinutes;
    }
    timeCounter = intervalCounter;

    timeCounter += durationInMinutes;
    if (timeCounter <= timespanInMinutes) {
      retVal++;
    }
  }
  return retVal;
};

const _calculateTotalResources = (occurrences, resources) => {
  const retVal = {
    sum: 0
  };
  Object.values(constants.RESOURCES).forEach((resource) => {
    retVal[resource] = occurrences * resources[resource];
    retVal.sum += retVal[resource];
  });
  return retVal;
};

const getUnsortedArrayOfLogisticsEfficiencies = (logisticsMap, activeInterval, timespan) => {
  const retVal = [];
  // sanitize inputs because users are evil
  // make negatives or empty strings 0
  activeInterval = {
    hours:
      Number.isInteger(activeInterval.hours) && activeInterval.hours > -1
        ? activeInterval.hours
        : 0,
    minutes:
      Number.isInteger(activeInterval.minutes) && activeInterval.minutes > -1
        ? activeInterval.minutes
        : 0
  };
  timespan = {
    hours: Number.isInteger(timespan.hours) && timespan.hours > -1 ? timespan.hours : 0,
    minutes: Number.isInteger(timespan.minutes) && timespan.minutes > -1 ? timespan.minutes : 0
  };

  Object.keys(logisticsMap).forEach((chapter) => {
    const chapterMap = logisticsMap[chapter];
    Object.keys(chapterMap).forEach((mission) => {
      const logisticObject = chapterMap[mission];
      const occurrences = _calculateOccurrencesPerIntervalPerTimespan(
        logisticObject.duration,
        activeInterval,
        timespan
      );
      const efficiency = _calculateEfficiency(occurrences, timespan, logisticObject.resources);
      const totalResources = _calculateTotalResources(occurrences, logisticObject.resources);
      // put the spread first and then the stuff we're augmenting the object with
      retVal.push({
        ...logisticObject,
        chapter,
        mission,
        occurrences,
        efficiency,
        totalResources
      });
    });
  });
  return retVal;
};

const trySetLocalStorage = (key, value) => {
  try {
    localStorage.setItem(key, value);
  } catch (ignored) {
    // ignored
  }
};

const tryGetLocalStorage = (key) => {
  try {
    return localStorage.getItem(key);
  } catch (ignored) {
    return undefined;
  }
};

const tryClearLocalStorage = () => {
  try {
    localStorage.clear();
  } catch (ignored) {
    // ignored
  }
};

export default {
  getUnsortedArrayOfLogisticsEfficiencies,
  trySetLocalStorage,
  tryGetLocalStorage,
  tryClearLocalStorage
};
export const INTERNAL_FOR_UNIT_TESTS = {
  _calculateOccurrencesPerIntervalPerTimespan,
  _calculateTotalResources,
  _calculateEfficiency
};
