import React, { Component } from 'react';
import constants from '../utils/constants';
import helpers from '../utils/helpers';

const { trySetLocalStorage, tryGetLocalStorage, tryClearLocalStorage } = helpers;
const LOCAL_STORAGE_PREFIX = 'GFL_';
const LOCAL_STORAGE_KEYS = {
  interval: {
    hours: `${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`,
    minutes: `${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`
  },
  timespan: {
    hours: `${LOCAL_STORAGE_PREFIX}_TIMESPAN_HOURS`,
    minutes: `${LOCAL_STORAGE_PREFIX}_TIMESPAN_MINUTES`
  },
  resourceWeights: {
    [constants.RESOURCES.MANPOWER]: `${LOCAL_STORAGE_PREFIX}_${constants.RESOURCES.MANPOWER}`,
    [constants.RESOURCES.AMMO]: `${LOCAL_STORAGE_PREFIX}_${constants.RESOURCES.AMMO}`,
    [constants.RESOURCES.RATIONS]: `${LOCAL_STORAGE_PREFIX}_${constants.RESOURCES.RATIONS}`,
    [constants.RESOURCES.PARTS]: `${LOCAL_STORAGE_PREFIX}_${constants.RESOURCES.PARTS}`,
    totalResources: `${LOCAL_STORAGE_PREFIX}_TOTAL_RESOURCES`
  },
  itemWeights: {
    [constants.ITEMS.T_DOLL_CONTRACT]: `${LOCAL_STORAGE_PREFIX}_${constants.ITEMS.T_DOLL_CONTRACT}`,
    [constants.ITEMS.EQUIP_CONTRACT]: `${LOCAL_STORAGE_PREFIX}_${constants.ITEMS.EQUIP_CONTRACT}`,
    [constants.ITEMS.QUICK_PRODUCTION]: `${LOCAL_STORAGE_PREFIX}_${
      constants.ITEMS.QUICK_PRODUCTION
    }`,
    [constants.ITEMS.QUICK_REPAIR]: `${LOCAL_STORAGE_PREFIX}_${constants.ITEMS.QUICK_REPAIR}`,
    [constants.ITEMS.TOKEN]: `${LOCAL_STORAGE_PREFIX}_${constants.ITEMS.TOKEN}`
  }
};

export default class UserEntry extends Component {
  step1Presets = {
    immediately: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`, 0);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`, 0);
      this.setState(
        {
          interval: {
            hours: 0,
            minutes: 0
          }
        },
        this._updateParentInterval
      );
    },
    every30minutes: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`, 0);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`, 30);
      this.setState(
        {
          interval: {
            hours: 0,
            minutes: 30
          }
        },
        this._updateParentInterval
      );
    },
    everyHour: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`, 1);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`, 0);
      this.setState(
        {
          interval: {
            hours: 1,
            minutes: 0
          }
        },
        this._updateParentInterval
      );
    },
    sleepFor8Hours: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`, 8);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`, 0);
      this.setState(
        {
          interval: {
            hours: 8,
            minutes: 0
          }
        },
        this._updateParentInterval
      );
    },
    sleepFor9Hours: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`, 9);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`, 0);
      this.setState(
        {
          interval: {
            hours: 9,
            minutes: 0
          }
        },
        this._updateParentInterval
      );
    },
    awayForADay: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`, 24);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`, 0);
      this.setState(
        {
          interval: {
            hours: 24,
            minutes: 0
          }
        },
        this._updateParentInterval
      );
    }
  };

  step2Presets = {
    awakeFor16Hours: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_HOURS`, 16);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_MINUTES`, 0);
      this.setState(
        {
          timespan: {
            hours: 16,
            minutes: 0
          }
        },
        this._updateParentTimespan
      );
    },
    sleepFor8Hours: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_HOURS`, 8);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_MINUTES`, 0);
      this.setState(
        {
          timespan: {
            hours: 8,
            minutes: 0
          }
        },
        this._updateParentTimespan
      );
    },
    sleepFor9Hours: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_HOURS`, 9);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_MINUTES`, 0);
      this.setState(
        {
          timespan: {
            hours: 9,
            minutes: 0
          }
        },
        this._updateParentTimespan
      );
    },
    awayForADay: () => {
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_HOURS`, 24);
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_MINUTES`, 0);
      this.setState(
        {
          timespan: {
            hours: 24,
            minutes: 0
          }
        },
        this._updateParentTimespan
      );
    }
  };

  constructor(props) {
    super(props);
    this.state = {
      interval: {
        hours: tryGetLocalStorage(LOCAL_STORAGE_KEYS.interval.hours) || 0,
        minutes: tryGetLocalStorage(LOCAL_STORAGE_KEYS.interval.minutes) || 0
      },
      timespan: {
        hours: tryGetLocalStorage(LOCAL_STORAGE_KEYS.timespan.hours) || 0,
        minutes: tryGetLocalStorage(LOCAL_STORAGE_KEYS.timespan.minutes) || 0
      },
      resourceWeights: {
        [constants.RESOURCES.MANPOWER]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.resourceWeights[constants.RESOURCES.MANPOWER]) || 0,
        [constants.RESOURCES.AMMO]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.resourceWeights[constants.RESOURCES.AMMO]) || 0,
        [constants.RESOURCES.RATIONS]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.resourceWeights[constants.RESOURCES.RATIONS]) || 0,
        [constants.RESOURCES.PARTS]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.resourceWeights[constants.RESOURCES.PARTS]) || 0,
        totalResources: tryGetLocalStorage(LOCAL_STORAGE_KEYS.resourceWeights.totalResources) || 1
      },
      itemWeights: {
        [constants.ITEMS.T_DOLL_CONTRACT]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.itemWeights[constants.ITEMS.T_DOLL_CONTRACT]) || 0,
        [constants.ITEMS.EQUIP_CONTRACT]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.itemWeights[constants.ITEMS.EQUIP_CONTRACT]) || 0,
        [constants.ITEMS.QUICK_PRODUCTION]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.itemWeights[constants.ITEMS.QUICK_PRODUCTION]) || 0,
        [constants.ITEMS.QUICK_REPAIR]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.itemWeights[constants.ITEMS.QUICK_REPAIR]) || 0,
        [constants.ITEMS.TOKEN]:
          tryGetLocalStorage(LOCAL_STORAGE_KEYS.itemWeights[constants.ITEMS.TOKEN]) || 0
      }
    };
    this._updateParentInterval();
    this._updateParentTimespan();
    this._updateParentResourceWeights();
    this._updateParentItemWeights();
  }

  _updateParentInterval = () => {
    this.props.updateInterval({
      hours: Number.parseInt(this.state.interval.hours, 10) || 0,
      minutes: Number.parseInt(this.state.interval.minutes, 10) || 0
    });
  };

  _updateParentTimespan = () => {
    this.props.updateTimespan({
      hours: Number.parseInt(this.state.timespan.hours, 10) || 0,
      minutes: Number.parseInt(this.state.timespan.minutes, 10) || 0
    });
  };

  _normalizeResourceWeights = (weights) => {
    const retVal = {};
    Object.values(constants.RESOURCES).forEach((resource) => {
      retVal[resource] = Number.parseInt(weights[resource], 10) || 0;
    });
    retVal.totalResources = Number.parseInt(weights.totalResources, 10) || 0;
    return retVal;
  };

  _updateParentResourceWeights = () => {
    this.props.updateResourceWeights(this._normalizeResourceWeights(this.state.resourceWeights));
  };

  _normalizeItemWeights = (weights) => {
    const retVal = {};
    Object.values(constants.ITEMS).forEach((item) => {
      retVal[item] = Number.parseInt(weights[item], 10) || 0;
    });
    return retVal;
  };

  _updateParentItemWeights = () => {
    this.props.updateItemWeights(this._normalizeItemWeights(this.state.itemWeights));
  };

  updateIntervalHours = (e) => {
    const targetValue = e.target.value;
    trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_HOURS`, targetValue);
    this.setState((state) => {
      return {
        interval: {
          hours: targetValue,
          minutes: state.interval.minutes
        }
      };
    }, this._updateParentInterval);
  };

  updateIntervalMinutes = (e) => {
    const targetValue = e.target.value;
    trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_INTERVAL_MINUTES`, targetValue);
    this.setState((state) => {
      return {
        interval: {
          hours: state.interval.hours,
          minutes: targetValue
        }
      };
    }, this._updateParentInterval);
  };

  updateTimespanHours = (e) => {
    const targetValue = e.target.value;
    trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_HOURS`, targetValue);
    this.setState((state) => {
      return {
        timespan: {
          hours: targetValue,
          minutes: state.timespan.minutes
        }
      };
    }, this._updateParentTimespan);
  };

  updateTimespanMinutes = (e) => {
    const targetValue = e.target.value;
    trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_TIMESPAN_MINUTES`, targetValue);
    this.setState((state) => {
      return {
        timespan: {
          hours: state.timespan.hours,
          minutes: targetValue
        }
      };
    }, this._updateParentTimespan);
  };

  updateResourceWeightConstructor = (resource) => {
    return (e) => {
      const targetValue = e.target.value;
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_${resource}`, targetValue);
      this.setState((state) => {
        return {
          resourceWeights: Object.assign({}, state.resourceWeights, {
            [resource]: targetValue
          })
        };
      }, this._updateParentResourceWeights);
    };
  };

  updateTotalResourcesWeight = (e) => {
    const targetValue = e.target.value;
    trySetLocalStorage(LOCAL_STORAGE_KEYS.resourceWeights.totalResources, targetValue);
    this.setState((state) => {
      return {
        resourceWeights: Object.assign({}, state.resourceWeights, {
          totalResources: targetValue
        })
      };
    }, this._updateParentResourceWeights);
  };

  updateItemWeightConstructor = (item) => {
    return (e) => {
      const targetValue = e.target.value;
      trySetLocalStorage(`${LOCAL_STORAGE_PREFIX}_${item}`, targetValue);
      this.setState((state) => {
        return {
          itemWeights: Object.assign({}, state.itemWeights, { [item]: targetValue })
        };
      }, this._updateParentItemWeights);
    };
  };

  resetValues = () => {
    tryClearLocalStorage();
    this.setState(
      {
        interval: {
          hours: 0,
          minutes: 0
        },
        timespan: {
          hours: 0,
          minutes: 0
        },
        resourceWeights: {
          [constants.RESOURCES.MANPOWER]: 0,
          [constants.RESOURCES.AMMO]: 0,
          [constants.RESOURCES.RATIONS]: 0,
          [constants.RESOURCES.PARTS]: 0,
          totalResources: 1
        },
        itemWeights: {
          [constants.ITEMS.T_DOLL_CONTRACT]: 0,
          [constants.ITEMS.EQUIP_CONTRACT]: 0,
          [constants.ITEMS.QUICK_PRODUCTION]: 0,
          [constants.ITEMS.QUICK_REPAIR]: 0,
          [constants.ITEMS.TOKEN]: 0
        }
      },
      () => {
        this._updateParentInterval();
        this._updateParentTimespan();
        this._updateParentResourceWeights();
        this._updateParentItemWeights();
      }
    );
  };

  render() {
    return (
      <div>
        <div style={{ padding: '10px' }}>
          <div style={{ fontSize: '18px', fontWeight: 'bold' }}>Step 0: Reset stored values</div>
          <div>
            If you used this calculator before and the values it stored for you are no longer valid,
            clear them with this button.
          </div>
          <br />
          <div>
            <button type="button" onClick={this.resetValues}>
              Reset all values
            </button>
          </div>
        </div>
        <hr />
        <div style={{ padding: '10px' }}>
          <div style={{ fontSize: '18px', fontWeight: 'bold' }}>
            Step 1: How often can you/do you want to check your phone?
          </div>
          <div>0 hours 0 minutes means you can check it immediately when a logistic finishes.</div>
          <div>
            If you are going to sleep, put down however long you sleep for unless you check your
            phone in your sleep.
          </div>
          <br />
          <div>
            Presets:{' '}
            <button type="button" onClick={this.step1Presets.immediately}>
              As soon as a logistic finishes
            </button>{' '}
            <button type="button" onClick={this.step1Presets.every30minutes}>
              Every 30 minutes
            </button>{' '}
            <button type="button" onClick={this.step1Presets.everyHour}>
              Every hour
            </button>{' '}
            <button type="button" onClick={this.step1Presets.sleepFor8Hours}>
              I am sleeping for 8 hours
            </button>{' '}
            <button type="button" onClick={this.step1Presets.sleepFor9Hours}>
              I am sleeping for 9 hours
            </button>{' '}
            <button type="button" onClick={this.step1Presets.awayForADay}>
              I am away for a day
            </button>
          </div>
          <br />
          <div>
            Every{' '}
            <input
              type="number"
              value={this.state.interval.hours}
              onChange={this.updateIntervalHours}
            />{' '}
            hours and{' '}
            <input
              type="number"
              value={this.state.interval.minutes}
              onChange={this.updateIntervalMinutes}
            />{' '}
            minutes
          </div>
        </div>
        <hr />
        <div style={{ padding: '10px' }}>
          <div style={{ fontSize: '18px', fontWeight: 'bold' }}>
            Step 2: How long are you running the logistics for?
          </div>
          <div>Typically people are awake 16 hours and asleep 8 hours a day.</div>
          <div>
            You can go beyond 24 hours if you plan on being away for more than a day, for example.
            But since there is no logistic that goes beyond 24 hours, the results will not change.
          </div>
          <br />
          <div>
            Presets:{' '}
            <button type="button" onClick={this.step2Presets.awakeFor16Hours}>
              I am awake for 16 hours
            </button>{' '}
            <button type="button" onClick={this.step2Presets.sleepFor8Hours}>
              I am sleeping for 8 hours
            </button>{' '}
            <button type="button" onClick={this.step2Presets.sleepFor9Hours}>
              I am sleeping for 9 hours
            </button>{' '}
            <button type="button" onClick={this.step2Presets.awayForADay}>
              I am away for a day
            </button>
          </div>
          <br />
          <div>
            <span>
              <input
                type="number"
                value={this.state.timespan.hours}
                onChange={this.updateTimespanHours}
              />{' '}
              hours and{' '}
              <input
                type="number"
                value={this.state.timespan.minutes}
                onChange={this.updateTimespanMinutes}
              />{' '}
              minutes
            </span>
          </div>
        </div>
        <hr />
        <div style={{ padding: '10px' }}>
          <div style={{ fontSize: '18px', fontWeight: 'bold' }}>
            Step 3: How much do you care about each of the following resources/items?
          </div>
          <div>
            0 means you do not care at all. 1 means you care. 2 means you care twice as much. 3
            means you care 3 times as much, etc.
          </div>
          <br />
          <table>
            <tbody>
              <tr>
                <th>
                  Total Resources
                  <br />
                  (Not Including Items)
                </th>
                <th>Manpower</th>
                <th>Ammo</th>
                <th>Rations</th>
                <th>Parts</th>
              </tr>
              <tr>
                <td>
                  <input
                    type="number"
                    value={this.state.resourceWeights.totalResources}
                    onChange={this.updateTotalResourcesWeight}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.resourceWeights[constants.RESOURCES.MANPOWER]}
                    onChange={this.updateResourceWeightConstructor(constants.RESOURCES.MANPOWER)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.resourceWeights[constants.RESOURCES.AMMO]}
                    onChange={this.updateResourceWeightConstructor(constants.RESOURCES.AMMO)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.resourceWeights[constants.RESOURCES.RATIONS]}
                    onChange={this.updateResourceWeightConstructor(constants.RESOURCES.RATIONS)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.resourceWeights[constants.RESOURCES.PARTS]}
                    onChange={this.updateResourceWeightConstructor(constants.RESOURCES.PARTS)}
                  />
                </td>
              </tr>
              <tr>
                <th>T-Doll Contracts</th>
                <th>Equipment Contracts</th>
                <th>Quick Production</th>
                <th>Quick Repair</th>
                <th>Tokens</th>
              </tr>
              <tr>
                <td>
                  <input
                    type="number"
                    value={this.state.itemWeights[constants.ITEMS.T_DOLL_CONTRACT]}
                    onChange={this.updateItemWeightConstructor(constants.ITEMS.T_DOLL_CONTRACT)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.itemWeights[constants.ITEMS.EQUIP_CONTRACT]}
                    onChange={this.updateItemWeightConstructor(constants.ITEMS.EQUIP_CONTRACT)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.itemWeights[constants.ITEMS.QUICK_PRODUCTION]}
                    onChange={this.updateItemWeightConstructor(constants.ITEMS.QUICK_PRODUCTION)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.itemWeights[constants.ITEMS.QUICK_REPAIR]}
                    onChange={this.updateItemWeightConstructor(constants.ITEMS.QUICK_REPAIR)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    value={this.state.itemWeights[constants.ITEMS.TOKEN]}
                    onChange={this.updateItemWeightConstructor(constants.ITEMS.TOKEN)}
                  />
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <hr />
      </div>
    );
  }
}
