import React from 'react';
import constants from '../utils/constants';

export default ({
  chapter, mission, duration, occurrences, efficiency, totalResources, items
}) => {
  return (
    <tr>
      <td>{`${chapter}-${mission}`}</td>
      <td>
        {duration.hours < 10 ? `0${duration.hours}` : duration.hours}:
        {duration.minutes < 10 ? `0${duration.minutes}` : duration.minutes}
      </td>
      <td>{occurrences}</td>
      {Object.values(constants.RESOURCES).map((value) => {
        const resourceValue = totalResources[value];
        return (
          <td key={value}>
            <div>{resourceValue}</div>
            {resourceValue > 0 && (
              <div>
                {efficiency[value]}
                /min
              </div>
            )}
          </td>
        );
      })}
      <td>{totalResources.sum}</td>
      <td>
        {Object.values(items).map((item) => {
          return <div key={item}>{item}</div>;
        })}
      </td>
    </tr>
  );
};
